.app-container {
  text-align: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
  padding: 40px 20px;
  font-family: 'Georgia', 'Times New Roman', serif;
  display: flex;
  flex-direction: column;
  align-items: center;
}


.card-versiculo {
  position: relative;
  width: 360px; /* Ligeiramente mais largo */
  padding: 80px 40px 30px 40px; /* Padding mais generoso */
  min-height: 220px; /* Mais altura para respiro */
  margin: 40px auto;
  background: linear-gradient(135deg, #fafbfc73 0%, #f1f5f9 100%);
  border: 1.5px solid #e2e8f0; /* Borda mais fina e refinada */
  box-shadow: 
    0 12px 40px rgba(71, 85, 105, 0.08),
    inset 0 1px 0 rgba(255, 255, 255, 0.8); /* Sombra mais suave + brilho interno */
  border-radius: 1.5rem; /* Cantos mais arredondados */
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Georgia', 'Times New Roman', serif; /* Tipografia clássica */
}

.card-capitulo {
  position: absolute;
  top: 20px; /* Mais espaço no topo */
  left: 24px;
  font-size: 2.2rem;
  font-weight: 600; /* Peso mais refinado */
  color: #4b5563; /* Cinza mais suave */
  opacity: 0.9;
  letter-spacing: 1px;
  font-family: 'Playfair Display', Georgia, serif; /* Fonte decorativa para números */
}

.card-versiculo-num {
  position: absolute;
  bottom: 20px; /* Mais espaço na base */
  right: 24px;
  font-size: 1.1rem;
  font-weight: 500;
  color: #6b7280;
  opacity: 0.7;
  letter-spacing: 0.5px;
  font-style: italic; /* Estilo mais elegante */
}

.card-livro {
  position: absolute;
  top: 22%; 
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 1.3rem;
  color: #9ca3af;
  opacity: 0.35;
  pointer-events: none;
  user-select: none;
  text-align: center;
  white-space: nowrap;
  z-index: 0;
  font-family: 'Georgia', serif;
  font-style: italic;
  letter-spacing: 1.5px;
}

.card-texto {
  position: relative;
  z-index: 1;
  font-size: 1.3rem;
  font-weight: 450; /* Peso mais leve e elegante */
  color: #374151; /* Cinza mais quente */
  text-align: center;
  margin: 0 30px;
  line-height: 1.5; /* Maior entrelinhamento */
  background: rgba(248, 249, 250, 0.7);
  padding: 12px 16px;
  border-radius: 8px; /* Cantos suaves no fundo do texto */
  font-family: 'Georgia', 'Times New Roman', serif;
  text-shadow: 0.5px 0.5px 1px rgba(255, 255, 255, 0.8); /* Sutil realce */
}

/* Efeito de brilho sutil no topo */
.card-versiculo::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    rgba(255, 255, 255, 0.8) 50%, 
    transparent 100%);
}

